<style scoped lang="less">
@import "../../assets/css/theme.less";
#details{
    .replyArea{
        margin-top:10px;
        padding-top:10px;
        textarea{
            resize: none;
        }
        .ctitle{
            margin-bottom:10px;
        }
    }
    .commitArea{
        position: relative;
        margin-top:10px;
        padding-top:10px;
        border-top:1px solid #ececec;
        .ctitle{
            margin-bottom:10px;
        }
        .editor{
            position: absolute;
            bottom:2px;
            right:5px;
            font-size: 10px;
        }
    }
    .imgArea{
        margin-top:10px;
        padding-top:10px;
        border-top:1px solid #ececec;
    }
    img{
        width:150px;
    }
}
.timeLine{
    position: relative;
    padding-top:20px;
    padding-bottom:30px;
    .timeLine-itemhead{
        font-size: 14px;
        border-bottom: 1px solid #ececec;
        padding-bottom: 5px;
    }
    .timeLine-itembody{
        padding-left: 10px;
        padding-top: 5px;
    }
    .timeLine-itemmark{
        position: absolute;
        bottom:2px;
        font-size: 12px;
        right:10px;
    }
    .timeLine-line{
        position: absolute;
        top:0;
        left: 0;
        height: 100%;
        width: 5px;
        background-color: @cut2;
        border-radius: 5px;
    }
    .timeLine-item{
        max-width: 650px;
        position: relative;
        background-color: #fff;
        margin-bottom: 20px;
        padding:10px 20px 20px 20px;
        border:1px solid @cut2;
        box-shadow: 2px 5px 10px #9d9d9d;
        border-radius: 8px;
    }
    .timeLine-item-back::before{
        content: '';
        position: absolute;
        width: 10px;
        height: 10px;
        background-color: #fff;
        border:2px solid @cut2;
        border-radius: 50%;
        top:calc(50% - 5px);
        left:-18px;
        z-index: 1;
    }
    .timeLine-item-back::after{
        content: '';
        position: absolute;
        width: 15px;
        height: 4px;
        background-color: #fff;
        border:1px solid @cut2;
        z-index: 0;
        top:calc(50% - 2px);
        left:-15px;
    }
}
.infoList{
  .infoItem::before{
    content:'';
    display: inline-block;
    margin-right: 5px;
    width: 10px;
    height:10px;
    border-radius: 50%;
    background-color:#e03232
  }
}
.row{
  .title{
      width:100%;
      background-color: #fff;
      margin:10px 0;
      padding:10px 20px;
  }
}
@media screen and (min-width:768px){
    .row{
        .title{
            border-left:5px solid @cut2;
        }
    }
}
@media screen and (max-width:768px){

}

.toprow{
  min-height: 400px;
  padding:0 5px;
  .box{
    background-color: #fff;
    margin-bottom:15px;
    padding:10px 20px;
  }
}


</style>



